@import url("https://fonts.googleapis.com/css?family=Varela+Round");
html, body {
  display: flex;
  flex-direction: column;
  flex: 1 0 100%;
  min-height: 100%; }

body {
  font-size: 16px;
  font-family: 'Varela Round', sans-serif; }

#app {
  display: flex;
  flex-direction: column;
  flex: 1 0 100%;
  min-height: 100%; }

main {
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 50px;
  font-size: 1rem; }

input {
  direction: rtl;
  text-align: right; }

.mobile-only {
  display: none; }

.sidebar-overlay.active {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 0.2;
  background: #000; }
  .main .sidebar-overlay.active {
    padding-top: 0; }

@media (max-width: 800px) {
  .desktop-only {
    display: none; }
  .mobile-only {
    display: block; } }

.md-input-container label {
  right: 15px; }

.calc-button, .calculation-card__actions__button {
  display: inline-block;
  height: 36px;
  line-height: 36px;
  padding: 0 2rem;
  border-radius: 2px;
  border: none;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase; }

.input-number, .input-date {
  position: relative;
  margin-bottom: 45px; }
  .input-number__label, .input-date__label {
    position: absolute;
    right: 5px;
    top: 10px;
    font-size: 1em;
    font-weight: normal;
    pointer-events: none; }
  .input-number__input, .input-date__picker__input {
    width: 100%;
    padding: 10px 10px 10px 5px;
    display: block;
    border: none;
    font-size: 1em; }
    .input-number__input:focus, .input-date__picker__input:focus {
      outline: none; }
    .input-number__input:focus ~ .input-number__label, .input-date__picker__input:focus ~ .input-number__label, .input-number__input:focus ~ .input-date__label, .input-date__picker__input:focus ~ .input-date__label, .input-number__input:valid ~ .input-number__label, .input-date__picker__input:valid ~ .input-number__label, .input-number__input:valid ~ .input-date__label, .input-date__picker__input:valid ~ .input-date__label {
      top: -20px;
      font-size: 0.750em;
      transition: 0.2s ease all; }

.input-date__picker .vdp-datepicker__calendar {
  width: 100%; }

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  padding: 0 50px;
  box-shadow: 0px 1px 20px grey; }
  .header__logo__img {
    height: 50px; }

@media (max-width: 800px) {
  .header {
    padding: 0 20px; }
    .header__logo {
      flex: 1;
      text-align: center; } }

.footer {
  padding: 20px 0;
  text-align: center; }

.navigation__list {
  display: flex;
  list-style-type: none; }
  .navigation__list__item {
    padding: 10px;
    margin: 0 10px; }
    .navigation__list__item__link {
      text-decoration: none; }

.sidebar {
  position: absolute;
  z-index: 999999;
  right: 0;
  top: 55px;
  display: none;
  width: 150px;
  height: 100%;
  padding: 0 20px;
  transform: translateX(150px);
  transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1); }
  .sidebar__close {
    position: absolute;
    top: 0;
    right: 5px;
    display: block;
    height: 10px;
    cursor: pointer; }
  .sidebar__navigation {
    margin: 20px 0; }
    .sidebar__navigation__list {
      padding: 10px 0; }
      .sidebar__navigation__list__item {
        padding: 5px 0;
        margin: 6px 0; }
  .sidebar--active {
    display: block;
    transform: translateX(0px); }
  .sidebar--left {
    left: 0; }

.calculation-card {
  display: flex;
  flex-direction: column;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2), 0 2px 2px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12); }
  .calculation-card__header {
    padding: 15px 24px;
    margin: 0 0 10px 0;
    align-items: center;
    border-bottom: 1px solid #efefef; }
  .calculation-card__content {
    flex: 1;
    padding: 24px; }
  .calculation-card__actions {
    padding: 24px; }
  .calculation-card:hover {
    box-shadow: 0 4px 9px rgba(0, 0, 0, 0.2), 0 4px 4px rgba(0, 0, 0, 0.14), 0 3px 3px -5px rgba(0, 0, 0, 0.12); }

@media (max-width: 1020px) {
  .calculation-card__content {
    display: flex; } }

.calculation-card__content__result__sign, .calculation-card__content__result__number {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1.250em;
  font-weight: bolder; }

.calculation-card__content__result__sign {
  margin: 0 0 45px 0; }

.switch-language {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0; }
  .switch-language__language {
    padding: 5px;
    margin: 5px;
    cursor: pointer; }

.result__value {
  display: block;
  width: 100%;
  padding: 5px 24px;
  font-size: 1.250em; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
