.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  padding: 0 50px;
  box-shadow: 0px 1px 20px grey;

  &__logo {
    &__img {
      height: 50px;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .header {
    padding: 0 20px;

    &__logo {
      flex: 1;
      text-align: center;
    }
  }
}



