%card {
  display: flex;
  flex-direction: column;
  border-radius: 2px;
  box-shadow: 0 1px 5px rgba(0,0,0,.2), 0 2px 2px rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12);

  &__header {
    padding: 15px 24px;
    margin: 0 0 10px 0;
    align-items: center;
    border-bottom: 1px solid #efefef;
  }

  &__content {
    flex: 1;
    padding: 24px;
  }

  &__actions {
    padding: 24px;

    &__button {
      @extend .calc-button;
    }
  }

  &:hover {
    box-shadow: 0 4px 9px rgba(0,0,0,.2), 0 4px 4px rgba(0,0,0,.14), 0 3px 3px -5px rgba(0,0,0,.12);
  }
}

@media (max-width: 1020px) {
  %card {
    &__content {
      display: flex;
    }
  }
}



